<template>
  <div class="wrap wrapper-content">
    <div class="ibox float-e-margins">
      <div class="ibox-content">
        <el-row :gutter="20">
          <el-form :model="searchForm" ref="searchForm" label-width="80px" size="small">
            <el-col :span="24">
              <cd-input label="客户姓名:" placeholder="请输入客户姓名" v-model="searchForm.customerName" prop="customerName" :offset="1"></cd-input>
              <cd-input label="证件号:" placeholder="请输入证件号" v-model="searchForm.cardNo" prop="cardNo"></cd-input>
              <cd-input label="贷款编号:" placeholder="请输入贷款编号" v-model="searchForm.projectNo" prop="projectNo"></cd-input>
            </el-col>
            <el-col :span="24">
              <cd-input label="登记人:" placeholder="请输入登记人" v-model="searchForm.addUser" prop="addUser" :offset="1"></cd-input>
              <cd-select label="发起机构:" v-model="searchForm.launchOrgId" :options="orgList" prop="launchOrgId"></cd-select>
              <cd-select label="合作银行:" v-model="searchForm.coBankId" :options="coBank" prop="coBankId"></cd-select>
            </el-col>
            <el-col :span="24">
              <cd-select label="名单制类型:" v-model="searchForm.overdueType" prop="overdueType" :options="listType" :offset="1"></cd-select>
              <cd-select label="风险类型:" v-model="searchForm.riskType" prop="riskType" :options="riskType"></cd-select>
            </el-col>
            <el-col :span="24">
              <cd-search-button @search="btnSearch" @reset="resetForm"></cd-search-button>
            </el-col>
          </el-form>
        </el-row>
      </div>
    </div>
    <div class="ibox float-e-margins">
      <div class="ibox-content">
        <el-row :gutter="20">
          <el-col :span="24" style="text-align: right;margin-bottom: 15px">
            <el-button type="primary" size="small" @click="exportExcel">导出excel</el-button>
            <el-button type="primary" size="small" @click="riskRegister">风险档案登记</el-button>
          </el-col>
          <cd-table
            :params="tableParams"
            :columns="columns"
            url="riskProfile/list"
            @operation="operate"
          ></cd-table>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import cdTable from '../../../components/Table.vue';
  import cdInput from '../../../components/Input.vue';
  import cdSelect from '../../../components/Select.vue';
  import cdSearchButton from '../../../components/searchButton.vue';
  import {getSystem,Fetch,getAllBank,getOrg,addTab,codeLibrary} from  '../../../common/js/iframe';
  export default{
    name: 'riskFileList',
    components: {
      'cd-table': cdTable,
      'cd-input': cdInput,
      'cd-select': cdSelect,
      'cd-search-button': cdSearchButton
    },
    data(){
      return {
        searchForm: {
          customerName: '',
          cardNo: '',
          projectNo: '',
          addUser: '',
          launchOrgId: '',
          coBankId: '',
          overdueType: '',
          riskType: ''
        },
        columns: [
          {prop: 'customerName',label:'客户姓名'},
          {prop: 'cardNo',label:'证件号'},
          {prop: 'projectNo',label:'贷款编号'},
          {prop: 'overdueTypeString',label:'名单制类型'},
          {prop: 'riskTypeString',label:'风险类型'},
          {prop: 'carType',label:'汽车类型',formatter: function (e,row,cellValue) {
            return ['','新车','二手车'][cellValue];
          }},
          {prop: 'bankPaymentAmount',label:'放款金额'},
          {prop: 'bankPaymentDate',label:'放款时间'},
          {prop: 'loanAmount',label:'贷款金额'},
          {prop: 'loanTermString',label:'期数'},
          {prop: 'coBankName',label:'合作银行'},
          {prop: 'launchOrgName',label:'发起机构'},
          {prop: 'operatorName',label:'登记人'},
          {prop: 'status',label:'处理状态',formatter: function (e,row,cellValue) {
            return ['','处理中','处理完成'][cellValue];
          }},
          {label:'操作',operations: [
            {label:'反馈登记',command:'feedBack',conditions:[{condition: 'status',value: 1}]},
            {label:'修改',command:'modify',conditions: [{condition: 'updateButton',value:1},{condition: 'status',value: 1}]},
            {label:'查看详情',command:'viewDetail'}
          ]},
        ],
        tableParams: {},
        orgList: [],
        coBank: [],
        listType:[],
        riskType:[]
      }
    },
    methods: {
      btnSearch(){
        this.tableParams = Object.assign({},this.searchForm);
      },
      resetForm(){
        this.$refs['searchForm'].resetFields();
      },
      exportExcel(){
        let search = '';
        for (let keys in this.searchForm){
          search += keys + '=' + this.searchForm[keys] + '&';
        }
        let params = search.substring(0,search.lastIndexOf('&'));
        var downLink = '/api/riskProfile/export?' + params;
        window.open(downLink, "_blank");
      },
      riskRegister(){
        addTab({
          title:'风险档案登记',
          href: './Modal/riskFile/riskRegister/riskRegister.html?type=1'
        })
      },
      operate(command){
        if(command.type == 'feedBack'){
          this.feedBack(command.item,command.index);
        }else if (command.type == 'modify') {
          this.modify(command.item,command.index);
        }else {
          this.viewDetail(command.item,command.index);
        }
      },
      feedBack(item,index){
        addTab({
          title:'反馈登记',
          href: './Modal/riskFile/riskRegister/riskRegister.html?type=4&id=' + item.id
        })
      },
      modify(item,index){
        addTab({
          title:'风险档案登记修改',
          href: './Modal/riskFile/riskRegister/riskRegister.html?type=2&id=' + item.id
        })
      },
      viewDetail(item,index){
        addTab({
          title:'查看风险档案详情',
          href: './Modal/riskFile/riskRegister/riskRegister.html?type=3&id=' + item.id
        })
      }
    },
    mounted(){
      this.tableParams = Object.assign({},this.searchForm);
      getSystem(this);
      getAllBank(this,'coBank');
      getOrg(this,'orgList');
      codeLibrary(this,{riskType:'riskType',overdueType:'listType'})
    }
  }
</script>




